/* MUERE! */

a, a:hover {
    display: inline-block;
    text-decoration: none;
    color: inherit; }
button {
    cursor: pointer !important; }

/* Colores Textos */

/* Textos Colores BÃ¡sicos */
.text-white {
    color: $white;
    &.text-shadow {
        text-shadow: 0px -1px 1px  rgba(darken($white,33),0.75); } }
.text-grey {
    color: $grey;
    &.text-shadow {
        text-shadow: 0px -1px 1px  rgba(darken($grey,33),0.75); } }
.text-black {
