//botones
.btn {
    display: block;
    height: 40px;
    background: none;
    border: solid 1px #6a6a6a;
    text-decoration: none;
    text-align: center;
    font-weight: 700;
    border-radius: 40px;
    -moz-border-radius: 40px;
    -webkit-border-radius: 40px;
    a {
        text-decoration: none; } }

/* Botones BÃ¡sicos */
.btn-white {
    @extend .btn;
    border-color: $white;
    color: $white;
    a {
        color: $white; }
    &:hover {
        background-color: $white; } }
.btn-grey {
    @extend .btn;
    border-color: $grey;
    color: $grey;
    a {
