// Utilidades
.wow {
    visibility: hidden;
    z-index: 9;
    &:hover {
        z-index: 99; } }

* {
    transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -moz-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -o-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275); }

.ripley-header {
    margin-bottom: 0; }
.ripley-footer {
    margin-top: 0s; }
.full-img {
    width: 100%; }
.scale {
    z-index: 9;
    &:hover {
        z-index: 99;
        @include scale(1.3);
        .descale {
            @include scale(0.8); } } }
.descale {
    z-index: 9; }
.scale-no-hover {
    @include scale(1.1); }
@include media-breakpoint-down(md) {
    .scale {
        &:hover {
            @include scale(1); } }
    .scale-no-hover {
        @include scale(1); } }
.relative {
    position: relative; }
.absolute {
    position: absolute; }
.card {
    @include shadow-4dp();
    @include border-radius(5);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    overflow: hidden;
    z-index: 9;
    &:hover {
        z-index: 99;
        @include shadow-8dp(); } }
.btn {
    min-width: 100px;
    &:hover {
        //@include shadow4()
        z-index: 1; } }
.pointer-events-none {
    pointer-events: none; }
.square {
    position: relative;
    width: 100%;
    overflow: hidden;
    &:after {
        content: "";
        display: block;
        padding-bottom: 100%; } }
.v-rectangle {
    position: relative;
    width: 100%;
    overflow: hidden;
    &:after {
        content: "";
        display: block;
        padding-bottom: 125%; } }
.h-rectangle {
    position: relative;
    width: 100%;
    overflow: hidden;
    &:after {
        content: "";
        display: block;
        padding-bottom: 66.666666666%; } }
.holder {
    //padding-bottom: 56.25%
    //@include min-height(400)
    position: absolute;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5); }
.inner-holder {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0);
    padding: 15px;
    opacity: 1;
    transition: opacity 1s cubic-bezier(.19,1,.22,1); }
.holder:hover {
    .inner-holder {
        opacity: 1;
        background: rgba(0, 0, 0, 0.125); } }
.full-height {
    height: 100vh; }
.bg-img {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat; }

//Sombras
.shadow-2dp {
    @include shadow-2dp(); }
.shadow-3dp {
    @include shadow-3dp(); }
.shadow-4dp {
    @include shadow-4dp(); }
.shadow-6dp {
    @include shadow-6dp(); }
.shadow-8dp {
    @include shadow-8dp(); }
.shadow-16dp {
    @include shadow-16dp(); }
.shadow-24dp {
    @include shadow-24dp(); }

//Clases para rotar
.rotate-5 {
    @include rotate(5); }
.rotate-10 {
    @include rotate(10); }
.rotate-15 {
    @include rotate(15); }
.rotate-cc-5 {
    @include rotate(-5); }
.rotate-cc-10 {
    @include rotate(-10); }
.rotate-cc-10 {
    @include rotate(-15); }


//nÃºmero
$base-numero: 50;
.numero {
    @include height($base-numero);
    @include width($base-numero);
    line-height:  ($base-numero) + px;
    background-color: $colorPrimario;
    text-align: center;
    margin: -($base-numero/2) + px auto 0;
    @include border-radius($base-numero);
    position: absolute;
    left: calc(50% - #{$base-numero/2}px);
    z-index: 9;
    @include font-size($base-numero);
    h1, h2 {
        margin: 0;
        line-height: ($base-numero) + px; } }
@include media-breakpoint-down(md) {
    .full-height {
        height: auto; } }
